* {
  z-index: 10;
}

.main {
  margin: 0;
  background-color:rgb(18, 29, 54);
  font-family: 'Courier New', monospace;
  color: whitesmoke;
  font-size: 1.2em;
  overflow-y: scroll;
  position: relative;
  display: flex;
  flex-direction: column;
}

.head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: min-content;
}

.terminal {
  height: 60vh;
}

.cursor {
  position: fixed;
  width: 200vw;
  height: 200vw;
  background: radial-gradient(circle, rgb(40, 40, 73) 0%, rgb(15, 23, 35) 20%, rgb(18, 29, 54) 100%);
  border-radius: 50%;
  pointer-events: none;
  transform: translate(-50%, -50%);
  overflow: hidden;
  z-index: -1;
}

.sidelinks {
  background-color: #f0f8ff;
  padding: 0.7rem 1rem;
  box-shadow: inset 4px 10px 10px 0px rgba(0, 0, 0, 1);
}

.sidelinks * {
  margin: 0.5rem 0;
  color: #0F172A;
}

.experienceskills,
.projectskills {
  background-color: #d9c2ff5d;
  color: #d9c2ff;
  margin-right: 0.2rem;
  padding: 0 0.5rem;
}

.experiencerow .date,
.educationrow .date {
  color: #d9c2ff;
}

@media (max-width: 768px) {
  .main {
    font-size: 1em;
  }
  .head {
    flex-direction: column;
    align-items: flex-start;
  }
  .main h1.text-7xl {
    font-size: 3rem;
    margin-left: 0;
  }
  .headingtext {
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }
  .cursor {
    display: none;
    }
    .sidelinks {
      display: none;
    }
  .experiencerow ul li,
  .projectrow ul li {
    margin-right: 0;
    margin-left: 0;
  }
  .educationrow .date {
    color: #d9c2ff;
  }
  .experiencerow h2,
  .educationrow h2,
  .projectrow h2,
  .experiencerow p,
  .educationrow p,
  .projectrow p,
  .experienceskills,
  .projectskills {
    font-size: 0.8em  ; 
  }

  .tbody{
    width: 100%;
  }
}