.terminal *{
  white-space: pre-wrap;
}

.terminal {
  background-color: #000;
  color: #d9c2ff;
  font-family: 'Courier New', monospace;
  padding: 20px;
  text-align: left;
  max-width: 800px;
  width: 80%;
  height: 80%;
  border-radius: 5px;
  box-shadow: 0 0 20px 5px #d9c2ff9c;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  font-size: .8em;
}

::-webkit-scrollbar{
  display: none;
}

.inputLine {
  display: flex;
  align-items: top;
}

.terminalInput {
  background: transparent;
  border: none;
  color: #d9c2ff;
  outline: none;
  width: 100%;
  font-family: 'Courier New', monospace;
  font-size: 1em;
}

/* Hangman.module.css */

.hangman {
  font-family: monospace;
  white-space: pre;
}

.hangmanConsole {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.hangmanConsole pre {
  margin: 0;
}

@media (max-width: 768px) {
  .hangmanConsole pre {
    font-size: 0.8em;
  }
}